import React, { useState, useEffect } from "react"

export default function Live() {
  const [data, setData] = useState(null)
  useEffect(() => {
    fetch("https://5e5fd2fbcbbe0600146cb3f2.mockapi.io/api/v1/devices", {
      method: "GET",
      headers: { "Content-Type": "application/json" },
    })
      .then(res => res.json())
      .then(res => {
        console.log(res)
        setData(res[0]?.name)
      })
  }, [])
  return (
    <>
      <h2>Live: {data}</h2>
    </>
  )
}
