import React from "react"
import { Link, graphql } from "gatsby"
import styled from "styled-components"
import { ToastContainer, toast } from "react-toastify"
import "react-toastify/dist/ReactToastify.css"
import Live from "../components/Live"

const BuyNowButton = styled.a`
  display: inline-block;
  text-align: center;
  width: 20%;
  margin: 2rem auto;
  background: rebeccapurple;
  border-radius: 6px;
  padding: 10px 20px;
  text-decoration: none;
  color: #fff;
`

const ToastCheckoutButton = styled(props => <Link {...props} />)`
  display: inline-block;
  margin-left: 12px;
  margin-top: 12px;
  padding: 6px 8px;
  border-radius: 4px;
  color: #2e2e2e;
  text-decoration: none;
  background: #fff;
`

const Product = styled.div`
  margin: 1rem auto;
  max-width: 320px;
  border: 1px dotted rebeccapurple;
  padding: 15px;
`

export default function Home({ data }) {
  const products = data.products.nodes
  const categories = data.categories.nodes

  const Msg = ({ name, wooId }) => (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignContent: "center",
      }}
    >
      <p style={{ margin: 0 }}>{`Let's buy ${name}`}</p>
      <ToastCheckoutButton
        to={`https://shop.twinstrippin.com/checkout/?add-to-cart=${wooId}`}
      >
        Checkout
      </ToastCheckoutButton>
    </div>
  )

  const notify = (name, wooId) => {
    console.log(`${name} is added`)
    toast.success(<Msg name={name} wooId={wooId} />)
  }

  return (
    <>
      <Live />
      <div
        style={{
          display: "flex",
          gap: "1rem",
          justifyContent: "center",
          textAlign: "center",
        }}
      >
        {categories &&
          categories.map(category => (
            <div
              key={category.id}
              style={{ border: "1px solid #2e2e2e", padding: 16 }}
            >
              <Link
                to={`/category/${category.slug}`}
                style={{ color: "inherit" }}
              >
                <h2>{category.name}</h2>
              </Link>
              <h4>{category.count}</h4>
            </div>
          ))}
      </div>
      {products.map(product => (
        <Product key={product.id}>
          <Link to={`/product/${product.slug}`}>
            <h2 style={{ position: "relative" }}>
              {product.name}
              {product.on_sale && (
                <span
                  style={{
                    fontSize: 14,
                    borderRadius: "100%",
                    background: "#e74c3c",
                    color: "#fff",
                    padding: 10,
                    position: "absolute",
                    right: 20,
                    top: 30,
                    transform: "rotate(20deg)",
                  }}
                >
                  SALE!
                </span>
              )}
            </h2>
          </Link>
          <Link to={`/category/${product.categories[0].slug}`}>
            <p>
              <strong>{product.categories[0].name}</strong>
            </p>
          </Link>
          <img src={product.images[0].src} alt="product" width="200" />
          <div
            dangerouslySetInnerHTML={{ __html: product.short_description }}
          />
          <h4>{product.price} </h4>
          <p
            style={{ color: `${product.stock_quantity > 0 ? `green` : `red`}` }}
          >
            {product.stock_quantity || `0`} in stock.
          </p>
          <div
            style={{
              margin: "0 auto",
              width: 200,
              display: "grid",
              placeItems: "center",
            }}
          >
            <BuyNowButton
              href="#0"
              onClick={() => notify(product.name, product.wooCommerceId)}
            >
              Buy
            </BuyNowButton>
          </div>
        </Product>
      ))}
      <ToastContainer position="top-right" autoClose={6000} />
    </>
  )
}

export const query = graphql`
  query {
    products: allWcProduct {
      nodes {
        id
        slug
        name
        on_sale
        total_sales
        catalog_visibility
        description
        short_description
        price
        regular_price
        sale_price
        average_rating
        categories {
          id
          name
          slug
        }
        images {
          src
          alt
        }
        wooCommerceId
        manage_stock
        stock_quantity
        stock_status
      }
      pageInfo {
        perPage
      }
    }
    categories: allWcCategory {
      nodes {
        id
        name
        wooCommerceId
        slug
        count
        image {
          src
          name
        }
      }
    }
  }
`
